.NavSuper {
  display: flex;
  justify-content: center;
  font-size: 18px;
  /* color: wheat; */

  position: sticky;
  top: 0;
  z-index: 20;
}

.NavContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 75%;

  /* border: 1px solid rgb(255, 255, 255); */
}

.Logo {
  /* border: 1px solid rgb(253, 2, 2); */
}

.Links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 50%;

  /* border: 1px solid rgb(47, 54, 255); */
}

.Links p {
  cursor: pointer;
}

.Socials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 120px;

  /* border: 1px solid greenyellow; */
}

.Movil {
  position: absolute;
  width: 0%;
  min-width: 0px;
  pointer-events: none;
  opacity: 1;
  right: 40px;

  /* border: 1px solid rgb(255, 47, 217); */
}

.Movil img {
  width: 0px;
}

.NavBtn {
  background: none;
  border: none;
}

/*=========== Media Queries ===========*/

@media screen and (max-width: 1200px) {
  .NavContainer {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .NavContainer {
    justify-content: left;
  }

  .Links {
    pointer-events: none;
    opacity: 1;
    width: 0%;
    min-width: 0px;
    font-size: 0px;

    /* border: 1px solid rgb(47, 54, 255); */
  }

  .Socials {
    pointer-events: none;
    opacity: 1;
    width: 0%;
    min-width: 0px;

    /* border: 1px solid greenyellow; */
  }

  .Socials img {
    width: 0%;
  }

  .Movil {
    position: absolute;
    width: 40px;
    pointer-events: auto;
    opacity: 1;
    right: 40px;

    /* border: 1px solid rgb(255, 47, 217); */
  }

  .Movil img {
    width: 40px;
  }
}

/*============Panel Movil============*/

.NavPanel-Closed {
  position: absolute;
  top: 60px;
  width: 95%;
  background: rgb(9, 53, 9);
  opacity: 0;
  pointer-events: none;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.NavPanel-Open {
  position: absolute;
  top: 60px;
  width: 95%;
  background: rgb(9, 53, 9);
  opacity: 0;
  pointer-events: none;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 10% 0%;
}

@media screen and (max-width: 850px) {
  .NavPanel-Closed {
    position: absolute;
    top: -60px;
    width: 95%;
    background: rgb(9, 53, 9);
    opacity: 0;
    pointer-events: none;
    transition: 0.3s ease-in-out;
  }

  .NavPanel-Open {
    opacity: 1;
    pointer-events: auto;
    transition: 0.3s ease-in-out;
  }
}

.PanelLinks {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0% 0%;
  padding-bottom: 10%;
}

.PanelSocials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 120px;
}
