.A1Super {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.A1Container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 70%;
  padding: 50px 0%;
}

.A1Left {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  text-align: justify;
  flex: 1;
}

.A1Left p {
  font-family: "Exo", sans-serif;
}

.A1Right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.A1Right img {
  height: 500px;
}

/*============Media Queries=============*/

@media screen and (max-width: 1000px) {
  .A1Container {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .A1Container {
    flex-direction: column;
    padding: 30px 0%;
  }

  .A1Left h1 {
    font-size: 24px;
  }

  .A1Right img {
    width: 290px;
  }
}
