.A2Super {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.A2Container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 70%;
  padding: 100px 0%;
}

.A2Right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  text-align: justify;
  flex: 1;
}

.A2Right p {
  font-family: "Exo", sans-serif;
}

.A2Left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.A2Left img {
  height: 500px;
}

/*============Media Queries=============*/

@media screen and (max-width: 1000px) {
  .A2Container {
    width: 90%;
  }
}

@media screen and (max-width: 850px) {
  .A2Container {
    flex-direction: column-reverse;
    padding: 30px 0%;
  }

  .A2Right h1 {
    font-size: 24px;
  }

  .A2Left img {
    width: 290px;
  }
}
