.FSuper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 150px;
}

.Fcontainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40%;
}

.FTitle {
  padding: 40px 0px;
}

.Wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: rgb(6, 6, 6);
  padding: 0px 20px;
  cursor: pointer;
  border-top: solid 2px wheat;
  font-size: 9px;
  z-index: 10;
  transition: 0.3s ease-in-out;
}

.Wrap:hover {
  background: rgb(26, 26, 26);
  transition: 0.3s ease-in-out;
}

.ArrowOpen {
  transition: 0.3s ease-in-out;
  transform: rotate(180deg);
  height: 30px;
}

.ArrowClosed {
  transition: 0.3s ease-in-out;
  height: 30px;
}

.DropOpen {
  padding: 5px 0px;
  max-height: 500px;
  transition: 0.5s ease-in-out;
  font-family: "Exo", sans-serif;
  font-size: 20px;
}

.DropClosed {
  padding: 5px 0px;
  max-height: 0px;
  transition: 0.5s ease-in-out;
  opacity: 0;
  font-family: "Exo", sans-serif;
  /* font-size: 0px; */
}

/*==============Media Queries===============*/

@media screen and (max-width: 1000px) {
  .Fcontainer {
    width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .Wrap {
    font-size: 8px;
  }
}
